﻿import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login';
import { AuthGuard } from './_guards';

import { IndexComponent } from './index/index.component';

const appRoutes: Routes = [
    {
        path: '',
        component: IndexComponent,
        pathMatch: 'full'
    },
    { 
        path: 'login',
        component: LoginComponent
    },
    
    {
        path: 'settings',
        loadChildren: './modules/settings/settings.module#SettingsModule',
    },
    {
        path: '',
        loadChildren: './modules/app-users/app-users.module#AppUsersModule',
    },
    {
        path: 'staff',
        loadChildren: './modules/staff/staff.module#StaffModule',
    },
    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes, { useHash: true, scrollPositionRestoration: 'enabled' });